import type { NextRequest } from "next/server"

// ‚úÖ ‰øÆÊîπ 1: ÂàáÊç¢Âõû Node.js ËøêË°åÊó∂Ôºå‰ª•ÊîØÊåÅÊõ¥Â§ßÁöÑÊñá‰ª∂ÂíåÊõ¥Á®≥ÂÆöÁöÑ‰∏ä‰º†
export const runtime = "nodejs" 

// ÂèØÈÄâÔºöËÆæÁΩÆÊúÄÂ§ßÊâßË°åÊó∂Èó¥ÔºàÁßíÔºâÔºåÈò≤Ê≠¢‰∏ä‰º†Â§ßÊñá‰ª∂Ë∂ÖÊó∂
export const maxDuration = 60; 

const DIFY_BASE_URL = process.env.DIFY_BASE_URL || "https://api.dify.ai/v1"
const DIFY_API_KEY = process.env.DIFY_API_KEY

export async function POST(request: NextRequest) {
  if (!DIFY_API_KEY) {
    return new Response(JSON.stringify({ error: "Dify API key not configured" }), {
      status: 500,
      headers: { "Content-Type": "application/json" },
    })
  }

  try {
    // üîê ‰ªé header Ëé∑ÂèñÁî®Êà∑Ë∫´‰ªΩÔºàmiddleware Â∑≤È™åËØÅÔºâ
    const headerUserId = request.headers.get("X-User-Id")
    
    const formData = await request.formData()
    const file = formData.get("file") as File
    // ‰ºòÂÖà‰ΩøÁî® header ‰∏≠ÁöÑ userIdÔºåÂÖ∂Ê¨°‰ΩøÁî® formData ‰∏≠ÁöÑ
    const userId = headerUserId || formData.get("user") as string 

    if (!file) {
      return new Response(JSON.stringify({ error: "No file provided" }), {
        status: 400,
        headers: { "Content-Type": "application/json" },
      })
    }

    // üîê ‰∫åÊ¨°È™åËØÅÔºöÁ°Æ‰øùÊúâÁî®Êà∑Ë∫´‰ªΩ
    if (!userId) {
      return new Response(JSON.stringify({ error: "Êú™ÊéàÊùÉËÆøÈóÆÔºåËØ∑ÂÖàÁôªÂΩï" }), {
        status: 401,
        headers: { "Content-Type": "application/json" },
      })
    }

    const difyFormData = new FormData()
    difyFormData.append("file", file)
    // ‚úÖ ‰øÆÊîπ 3: ‰ΩøÁî®ÁúüÂÆûÁöÑÁî®Êà∑ IDÔºå‰øùÊåÅ‰∏éÂØπËØùÊé•Âè£‰∏ÄËá¥
    difyFormData.append("user", userId || "anonymous-user") 

    // Dify Êñá‰ª∂‰∏ä‰º†ÁöÑÊ≠£Á°ÆÁ´ØÁÇπ
    const uploadUrl = `${DIFY_BASE_URL}/files/upload`

    const response = await fetch(uploadUrl, {
      method: "POST",
      headers: {
        Authorization: `Bearer ${DIFY_API_KEY}`,
        // Ê≥®ÊÑèÔºö‰ΩøÁî® fetch ÂèëÈÄÅ FormData Êó∂ÔºåÈÄöÂ∏∏‰∏çÈúÄË¶ÅÊâãÂä®ËÆæÁΩÆ Content-TypeÔºå
        // ÊµèËßàÂô®/NodeÁéØÂ¢É‰ºöËá™Âä®ÁîüÊàêÂ∏¶ boundary ÁöÑÊ≠£Á°Æ Header
      },
      body: difyFormData,
    })

    if (!response.ok) {
      const errorText = await response.text()
      console.error("[Backend] Dify upload failed:", {
        status: response.status,
        url: uploadUrl,
        error: errorText,
      })

      return new Response(
        JSON.stringify({
          error: "File upload to Dify failed",
          details: errorText,
          status: response.status,
        }),
        {
          status: response.status,
          headers: { "Content-Type": "application/json" },
        },
      )
    }

    const data = await response.json()
    console.log("[Backend] Dify upload success:", { fileId: data.id, fileName: file.name, userId })

    return new Response(JSON.stringify(data), {
      status: 200,
      headers: { "Content-Type": "application/json" },
    })
  } catch (error) {
    console.error("[Backend] Upload error:", error)
    return new Response(
      JSON.stringify({
        error: "Internal server error",
        details: error instanceof Error ? error.message : String(error),
      }),
      {
        status: 500,
        headers: { "Content-Type": "application/json" },
      },
    )
  }
}
